import React, { Component } from 'react'
import { Button, Form, Tabs, Tab, Card, Media, Table } from 'react-bootstrap'
import Select from 'react-select'
import loadable from '@loadable/component'
import '../vendor/libs/react-quill/typography.scss'
import '../vendor/libs/react-quill/editor.scss'
import '../vendor/libs/react-select/react-select.scss'
import '../vendor/styles/pages/users.scss'

const LANGUAGES = [
    { value: 'English', label: 'English' },
    { value: 'German', label: 'German' },
    { value: 'French', label: 'French' }
]

const optionsWho = [
    { value: 'women', label: 'Women', color: '#00B8D9' },
    { value: 'men', label: 'Men', color: '#0052CC'},
    { value: 'lgbtiq', label: 'LGBTIQ', color: '#5243AA' },
    { value: 'kids', label: 'Kids', color: '#FF5630' },
];

const optionsLevel = [
    { value: 'beginner', label: 'Beginner', color: '#00B8D9' },
    { value: 'intermediate', label: 'Intermediate', color: '#0052CC'},
    { value: 'advanced', label: 'Advanced', color: '#5243AA' },
];

// Catch error in IE10
const ReactQuill = loadable(() => new Promise(resolve =>
import('react-quill').then(Component => resolve(Component)).catch(() => resolve(<div />))
))

class Page2 extends Component {
  constructor(props) {
      super(props)
      props.setTitle('Login v3 - Pages')

      this.state = {
          userData: {
              avatar: 'ws/user.png',
              firstname: 'Alexey',
              lastname: 'Furmenkov',
              username: 'nmaxwell',
              email: 'test@mail.ru',
              birthdate: '28.12.1998',
              company: 'Company Ltd.',
              id: 3425433,
              verified: true,
              role: 1,
              status: 1,

              permissions: [
                  { module: 'Users', read: true, write: false, create: false, delete: false },
                  { module: 'Articles', read: true, write: true, create: true, delete: false },
                  { module: 'Staff', read: false, write: false, create: false, delete: false }
              ],

              info: {
                  birthday: 'May 3, 1995',
                  country: 'Canada',
                  languages: ['English'],
                  phone: '+0 (123) 456 7891',
                  website: '',
                  music: ['Rock', 'Alternative', 'Electro', 'Drum & Bass', 'Dance'],
                  movies: ['The Green Mile', 'Pulp Fiction', 'Back to the Future', 'WALLÂ·E', 'Django Unchained', 'The Truman Show', 'Home Alone', 'Seven Pounds'],

                  twitter: 'https://twitter.com/user',
                  facebook: 'https://www.facebook.com/user',
                  google: '',
                  linkedin: '',
                  instagram: 'https://www.instagram.com/user'
              }
          },
          // multiValueFor: ["women", "men", "lgbtiq"],
          multiValueFor: [{ value: 'women', label: 'Women'},{ value: 'men', label: 'Men'}],
          multiValueLevel: [
              { value: 'intermediate', label: 'Intermediate'},
              { value: 'advanced', label: 'Advanced'},
          ]
      }
  }

    onValueChange(field, e, valType = String) {
        this.setState({
            userData: {
                ...this.state.userData,
                [field]: field === 'verified' ? e.target.checked : valType(e.target.value)
            }
        })
    }

    onPermissionChange(permission, field, e) {
        permission[field] = e.target.checked
        this.setState({
            userData: {
                ...this.state.userData,
                permissions: [...this.state.userData.permissions]
            }
        })
    }

    onInfoValueChange(field, e, valType = String) {
        this.setState({
            userData: {
                ...this.state.userData,
                info: {
                    ...this.state.userData.info,
                    [field]: valType(e.target.value)
                }
            }
        })
    }

    getLanguageOptions(langs) {
        return langs.reduce((result, lang) => {
            const found = LANGUAGES.find(l => l.value === lang)
            if (found) result.push(found)
            return result
        }, [])
    }

    getSelectObject(arr) {
        return arr.map(val => ({ value: val, label: val }))
    }

    onInfoSelectChange(field, e) {
        let val
        if (Array.isArray(e)) {
            val = e.map(obj => obj.value)
        } else if (e.target.multiple) {
            val = []
            const options = e.target.options
            for (let i = 0, l = options.length; i < l; i++) {
                if (options[i].selected) val.push(options[i].value)
            }
        } else {
            val = e.target.value.split(',').map(tag => tag.replace(/^\s+|\s+$/g, ''))
        }

        this.setState({
            userData: {
                ...this.state.userData,
                info: {
                    ...this.state.userData.info,
                    [field]: val
                }
            }
        })
    }

    prevent(e) {
        e.preventDefault()
    }
  render() {
      const modules = {
          toolbar: [
              ['bold', 'italic'],
              [{ 'list': 'ordered' }, { 'list': 'bullet' }],
              ['clean']
          ]
      }
      return (
          <div>
              <h4 className="font-weight-bold py-3 mb-4">
                  Edit <span className="text-muted">- KÃ¶ln Beavers</span>
              </h4>

              <div className="nav-tabs-top nav-responsive-sm">
                  <Tabs defaultActiveKey="account">
                      <Tab eventKey="account" title="About">
                          <Card.Body className="pb-2">

                              <Form.Group>
                                  <Form.Label>Sport *</Form.Label>
                                  <Form.Control value="Basketball" onChange={e => this.onValueChange('email', e)} className="mb-1" />
                              </Form.Group>

                              <Form.Group>
                                  <Form.Label>Name of the Club / Team *</Form.Label>
                                  <Form.Control value="KÃ¶ln Beavers" onChange={e => this.onValueChange('firstname', e)} />
                              </Form.Group>

                              <Form.Group>
                                  <Form.Label>Short Description <span className="text-muted">(anything that matters)</span></Form.Label>
                                  <Form.Control value="Basketball club for everyone" onChange={e => this.onValueChange('lastname', e)} />
                              </Form.Group>

                              <Form.Group>
                                  <Form.Label>Country *</Form.Label>
                                  <Form.Control value="Germany" onChange={e => this.onValueChange('country', e)} />
                              </Form.Group>
                              <Form.Group>
                                  <Form.Label>City *</Form.Label>
                                  <Form.Control value="KÃ¶ln" onChange={e => this.onValueChange('city', e)} />
                              </Form.Group>
                              <Form.Group>
                                  <Form.Label>Postal Code</Form.Label>
                                  <Form.Control value="47829" onChange={e => this.onValueChange('postalcode', e)} />
                              </Form.Group>
                              <Form.Group>
                                  <Form.Label>Address</Form.Label>
                                  <Form.Control value="Brehmstr. 22" onChange={e => this.onValueChange('postalcode', e)}/>
                              </Form.Group>
                              <Form.Group>
                                  <Form.Label>Contact Phone</Form.Label>
                                  <Form.Control value="+49 11457438475" onChange={e => this.onValueChange('postalcode', e)}/>
                              </Form.Group>
                              <Form.Group>
                                  <Form.Label>Website</Form.Label>
                                  <Form.Control value="koelnbeavers.de" onChange={e => this.onValueChange('postalcode', e)}/>
                              </Form.Group>

                              <div className="text-right mt-3">
                                  <Button variant="primary">Save</Button>&nbsp;
                                  <Button variant="default">Back to Dashboard</Button>
                              </div>
                              <br/>
                          </Card.Body>


                      </Tab>
                      <Tab eventKey="password" title="Additional Info">
                          <Card.Body className="pb-2">

                              <Form.Group>
                                  <Form.Label>Training Schedule</Form.Label>
                                  <ReactQuill modules={modules}
                                              onChange={this.onContentChange} value="<b>Monday</b><br/>10:00 - 11:00<br/><b>Wednesday</b><br/>18:00 - 19:00" />
                              </Form.Group>

                              <Form.Group>
                                  <Form.Label>Who Is It For</Form.Label>
                                  <Select isMulti className="react-select react-select-primary" classNamePrefix="react-select"
                                          options={optionsWho}
                                          defaultValue={this.state.multiValueFor} />
                              </Form.Group>

                              <Form.Group>
                                  <Form.Label>Level</Form.Label>
                                  <Select isMulti className="react-select react-select-secondary" classNamePrefix="react-select"
                                          options={optionsLevel}
                                          defaultValue={this.state.multiValueLevel} />
                              </Form.Group>

                              <Form.Group>
                                  <Form.Label>What do your new athletes need to bring with them for the first practice? <span className="text-muted">(e.g. special shoes, balls, warm clothes, etc)</span></Form.Label>
                                  <Form.Control />
                              </Form.Group>

                              <div className="text-right mt-3">
                                  <Button variant="primary">Save</Button>&nbsp;
                                  <Button variant="default">Back to Dashboard</Button>
                              </div>

                              <br/>
                          </Card.Body>

                      </Tab>
                  </Tabs>
              </div>

          </div>
      )
  }
}

export default Page2
